var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("黑猫")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        黑猫是一个虚构的人物出现在美国漫画书的出版奇迹漫画。在她的历史中，黑猫有时候是一个敌人，爱的利益，以及超级英雄蜘蛛侠的盟友。由作家Marv Wolfman和艺术家Dave Cockrum创作，她首次出现在The Amazing Spider-Man＃194（1979年 7 月）。")]),_c('blockquote',{staticClass:"blockquote"},[_vm._v("        Felicia Hardy是世界着名的猫咪窃贼Walter Hardy的女儿。在作为大学新生遭受创伤后，她训练自己的各种战斗风格和杂技，并在决定跟随父亲的脚步后，采用了黑猫的服装身份。作为黑猫，她具有影响概率场的潜意识能力，为她的敌人制造“厄运”。这个角色最初是蜘蛛侠的超级恶魔和对手，但随着时间的推移，这两个人坠入爱河，这促使她成为一个反叛乱者和他的伴侣。然而，他们之间的关系变得复杂，因为很明显黑猫只被吸引到了改变蜘蛛侠的自我，对英雄的平民生活没什么兴趣，就像彼得帕克一样。在他们分手之后，黑猫继续扮演蜘蛛侠最值得信赖的盟友之一的角色，多年来，这对夫妇分享了一次又一次的浪漫故事，然后她最终陷入了犯罪行为（很多蜘蛛侠的沮丧）和烦恼）。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260","src":""}})],1),_c('br'),_c('h2',[_vm._v("创作背景")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('br'),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        1979年，创作者Marv Wolfman正在为蜘蛛女寻找女性陪衬。他决定在Tex Avery卡通片“ Bad Luck Blackie ”中加入一个角色，其中一只黑猫给近距离的人带来了不幸。Black Cat的服装和外观由Dave Cockrum设计。当Wolfman将漫威漫画中的作业改为The Amazing Spider-Man时，他带着他的角色。他和Cockrum在这个时期对角色和她的外表做了相当大的改变; 打算在“ 蜘蛛侠”中登场的黑猫只有她的名字和权力与最终出现在“神奇蜘蛛侠”中的人一样。在底部字母页的惊人蜘蛛侠＃194，缩略图用于预期盖的蜘蛛妇女＃9显示连同为被拒绝的盖的蜘蛛侠＃194。沃尔夫曼在接受采访时说：“我没有计划将黑猫放在蜘蛛侠身上。我是为蜘蛛女人创造她的（看看公元前第一个故事的字母栏，你会看到）。然后我决定离开蜘蛛- 女人，让她感动了。所以，当我做她的时候，我从未想过猫女。我从Tex Avery卡通片中得到了她的想法，Bad Luck Blackie。“")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        作家兼导演凯文史密斯于2002年开始创作“ 蜘蛛侠/黑猫：邪恶男人做迷你剧”。在第三期后，该系列剧中断，直到2005年史密斯透露他最终写完了剧本。史密斯曾说过，“虽然我对自己的迟到没有任何防御（特别是当像本迪斯这样的人每月在多本书中发现好故事时），我会这样说：现在这是一个比它更好的故事。”我已经在02年完成了它。在2000年代中期，她与Wolverine一起出演了一部名为Claws的限量漫画迷你剧。这部迷你剧的续集，名为Claws II，于2011年7月开始发行。黑猫是2006-2007 英雄租用系列的主角。")])]),_c('v-card',{staticClass:"card-img"},[_c('v-img',{attrs:{"width":"180","height":"240","src":""}}),_c('v-card-title',{attrs:{"primary-title":""}},[_c('div',[_c('span')])])],1)],1),_c('br')])}
